.wrapper {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  position: relative;
  background-color: #F7F7F7;
}
@media screen and (max-width: 321px) {
  .wrapper {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper {
    /*大*/
    font-size: 17px;
  }
}
.wrapper::before,
.wrapper::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper::before {
  top: 0;
}
.wrapper::after {
  display: none;
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper::before,
  .wrapper::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper::before,
  .wrapper::after {
    transform: scaleY(0.33);
  }
}
.wrapper_left {
  flex: 1;
  padding: 4px 20px 4px 10px;
  background-color: #FFF;
  color: #AEAEAE;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrapper_right {
  flex: 1;
  display: flex;
  color: #AEAEAE;
  text-align: center;
}
.wrapper_right > li {
  flex: 1;
  padding: 0 10px;
}
.wrapper_right > li > i {
  margin-right: 2px;
}
.wrapper_right > li:not(:last-child) {
  position: relative;
}
.wrapper_right > li:not(:last-child)::before,
.wrapper_right > li:not(:last-child)::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  border-left: 1px solid #DDD;
  height: 100%;
  top: 0;
  bottom: 0;
}
.wrapper_right > li:not(:last-child)::before {
  display: none;
  left: 0;
}
.wrapper_right > li:not(:last-child)::after {
  right: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_right > li:not(:last-child)::before,
  .wrapper_right > li:not(:last-child)::after {
    transform: scaleX(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_right > li:not(:last-child)::before,
  .wrapper_right > li:not(:last-child)::after {
    transform: scaleX(0.33);
  }
}
