.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.wrapper_body {
  flex: 1;
  overflow-y: auto;
}
.wrapper_body_title {
  flex: 1;
  display: flex;
}
.wrapper_body_title > div:first-child {
  flex: 1;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  margin-right: 15px;
}
.wrapper_body_title > div:last-child {
  flex: 0 0 60px;
  font-size: 14px;
  text-align: center;
}
.wrapper_body_title > div:last-child > i {
  font-size: 20px;
}
@media screen and (max-width: 321px) {
  .wrapper_body_info {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_body_info {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_body_info {
    /*大*/
    font-size: 15px;
  }
}
.wrapper_body_info_des {
  display: flex;
  padding-bottom: 15px;
  position: relative;
}
.wrapper_body_info_des::before,
.wrapper_body_info_des::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper_body_info_des::before {
  display: none;
  top: 0;
}
.wrapper_body_info_des::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_body_info_des::before,
  .wrapper_body_info_des::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_body_info_des::before,
  .wrapper_body_info_des::after {
    transform: scaleY(0.33);
  }
}
.wrapper_body_info_des > ul {
  flex: 0 0 120px;
  max-width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  position: relative;
}
.wrapper_body_info_des > ul::before,
.wrapper_body_info_des > ul::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  border-left: 1px solid #DDD;
  height: 100%;
  top: 0;
  bottom: 0;
}
.wrapper_body_info_des > ul::before {
  display: none;
  left: 0;
}
.wrapper_body_info_des > ul::after {
  right: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_body_info_des > ul::before,
  .wrapper_body_info_des > ul::after {
    transform: scaleX(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_body_info_des > ul::before,
  .wrapper_body_info_des > ul::after {
    transform: scaleX(0.33);
  }
}
.wrapper_body_info_des > ul > li {
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrapper_body_info_des > ul > li:last-child {
  color: #AEAEAE;
}
.wrapper_body_info_des > div {
  flex: 1;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  color: #AEAEAE;
}
