@media screen and (max-width: 321px) {
  .item_header_title > li:first-child {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .item_header_title > li:first-child {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .item_header_title > li:first-child {
    /*大*/
    font-size: 17px;
  }
}
.item_header_title > li:last-child {
  color: #AEAEAE;
}
@media screen and (max-width: 321px) {
  .item_header_title > li:last-child {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .item_header_title > li:last-child {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .item_header_title > li:last-child {
    /*大*/
    font-size: 15px;
  }
}
.item_header_avatar {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #F7F7F7;
}
.item_header_avatar > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.item_header_reply {
  padding: 6px 12px;
  background-color: #F7F7F7;
}
@media screen and (max-width: 321px) {
  .item_header_reply {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .item_header_reply {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .item_header_reply {
    /*大*/
    font-size: 17px;
  }
}
@media screen and (max-width: 321px) {
  .item_body_box {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .item_body_box {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .item_body_box {
    /*大*/
    font-size: 17px;
  }
}
.item_body_box > li:first-child {
  word-break: break-all;
}
.item_body_box > :last-child {
  margin-top: 15px;
  padding-top: 10px;
  position: relative;
}
.item_body_box > :last-child::before,
.item_body_box > :last-child::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.item_body_box > :last-child::before {
  top: 0;
}
.item_body_box > :last-child::after {
  display: none;
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .item_body_box > :last-child::before,
  .item_body_box > :last-child::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .item_body_box > :last-child::before,
  .item_body_box > :last-child::after {
    transform: scaleY(0.33);
  }
}
.item_body_box > :last-child .tip {
  color: #34A5EA;
}
